import React, { useState } from 'react';
import MarkdownIt from 'markdown-it';
import MdEditor from 'react-markdown-editor-lite';
import 'react-markdown-editor-lite/lib/index.css';

const mdParser = new MarkdownIt();

function MarkdownEditor() {
  const [markdown, setMarkdown] = useState("# Write your Markdown here");
  const [noteTitle, setNoteTitle] = useState("");
  const [notes, setNotes] = useState([]);

  const handleEditorChange = ({ text }) => {
    setMarkdown(text);
  };

  const handleNoteTitleChange = (event) => {
    setNoteTitle(event.target.value);
  };

  const handleAddNote = () => {
    if (noteTitle.trim() !== "") {
      setNotes([...notes, { title: noteTitle, content: markdown }]);
      setNoteTitle("");
      setMarkdown("# Write your Markdown here");
    }
  };

  return (
    <div>
       
      <MdEditor
        value={markdown}
        style={{ height: '500px' }}
        renderHTML={(text) => mdParser.render(text)}
        onChange={handleEditorChange}
      />
      <div className="preview">
        <h2>Preview</h2>
        <div dangerouslySetInnerHTML={{ __html: mdParser.render(markdown) }} />
      </div>
      <div>
        
        <input
          type="text"
          placeholder="Note Title"
          value={noteTitle}
          onChange={handleNoteTitleChange}
        />
        <button onClick={handleAddNote}>Add Note</button>
      </div>
      <div>
        <h2>Notes</h2>
        <ul>
          {notes.map((note, index) => (
            <li key={index}>
              <h3>{note.title}</h3>
              <div dangerouslySetInnerHTML={{ __html: mdParser.render(note.content) }} />
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default MarkdownEditor;
